.restart {
	cursor: pointer;
	left: 0;
	top: 0;
	z-index: 5;
}

.restart.hide {
	position: static;
}

.restart.show {
	position: absolute;
	width: 600px;
	height: 600px;
}

@media only screen and (max-height: 630px) {
	.restart.show {
		position: absolute;
		width: 300px;
		height: 300px;
	}
}
